import {
  Component,
  Directive,
  QueryList,
  ContentChildren,
  Input,
  AfterViewInit,
  OnDestroy,
  ElementRef,
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  forwardRef,
  OnInit,
  Renderer2,
  ViewChild,
  ViewEncapsulation
} from '@angular/core';
import { DomSanitizer, SafeStyle } from '@angular/platform-browser';
import { Platform, LyTheme2, toBoolean } from '@alyle/ui';

const styles = ({
  root: {
    display: 'block',
    '-webkit-user-select': 'none',
    '-moz-user-select': 'none',
    '-ms-user-select': 'none',
    position: 'relative',
    '& .ly-carousel-actions': {
      position: 'absolute',
      top: 0,
      bottom: 0,
      margin: 'auto .25em',
      height: '1em',
      width: '1em',
      fontSize: '36px',
      cursor: 'pointer',
      color: '#fff',
      background: 'rgba(0, 0, 0, 0.11)',
      willChange: 'transform'
    },
    '& .ly-carousel-actions.right': {
      right: 0,
      '-webkit-transform': 'rotate(180deg)',
      transform: 'rotate(180deg)'
    },
    '& svg': {
      display: 'block',
      fill: 'currentColor'
    }
  },
  slideContainer: {
    overflow: 'hidden',
    display: 'block',
    width: '100%',
    height: '100%',
    position: 'relative'
  },
  slide: {
    display: 'flex',
    width: '100%',
    height: '100%',
    willChange: 'transform',
    '& > ly-carousel-item': {
      width: '100%',
      flexShrink: 0,
      overflow: 'auto',
      position: 'relative',
      backgroundSize: 'cover',
      backgroundPosition: 'center',
      backgroundRepeat: 'no-repeat'
    },
    '& > ly-carousel-item > [lyCarouselImg]': {
      width: '100%'
    }
  },
  slideContent: {
    display: 'flex'
  },
  slideAnim: {
    '& > div': {
      transition: 'transform 750ms cubic-bezier(.1, 1, 0.5, 1)'
  }
  },
  slideNoEvent: {
    '&>div': {
      touchAction: 'initial !important'
    }
  },
  carouselIndicators: {
    position: 'absolute',
    bottom: 0,
    left: 0,
    right: 0,
    margin: 0,
    boxSizing: 'border-box',
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
    height: '48px',
    '&>div': {
      display: 'inline-block',
      borderRadius: '50%',
      cursor: 'pointer',
      position: 'relative',
      padding: '.5em',
      outline: 'none'
    },
    '&>div > span': {
      transition: '300ms cubic-bezier(0.65, 0.05, 0.36, 1)',
      width: '1em',
      height: '1em',
      transform: 'scale(.5)',
      borderRadius: '50%',
      willChange: 'transform',
      display: 'block'
    },
    '&>div>span.active': {
      transform: 'scale(1)'
    }
  }
});

export enum CarouselMode {
  /** full */
  default,
  inline
}

@Component({
  selector: 'ly-carousel',
  template: `<div
(slidestart)="slideEvent && onDragStart($event)"
(slide)="slideEvent && onDrag($event)"
(slideend)="slideEvent && onDragEnd($event)"
#slideContainer
>
  <div [className]="classes.slide" [style.transform]="_positionLeft">
    <ng-content></ng-content>
  </div>
  <div [className]="classes.carouselIndicators" *ngIf="lyItems.length !== 1">
      <div tabindex="0"
      (click)="select(i)"
      role="button"
      *ngFor="let item of lyItems; let i = index">
      <span color="#000"
      bg="background:primary"
      [class.active]="selectedIndex==i"
      [elevation]="6"></span>
      </div>
  </div>
  <div class="ly-carousel-actions" (click)="prev()">
    <svg viewBox="0 0 24 24"><path d="M15.41 7.41L14 6l-6 6 6 6 1.41-1.41L10.83 12z"></path></svg>
  </div>
  <div class="ly-carousel-actions right" (click)="next()">
    <svg viewBox="0 0 24 24"><path d="M15.41 7.41L14 6l-6 6 6 6 1.41-1.41L10.83 12z"></path></svg>
  </div>
</div>`,
  changeDetection: ChangeDetectionStrategy.OnPush,
  preserveWhitespaces: false,
  encapsulation: ViewEncapsulation.None
})
export class LyCarousel implements OnInit, AfterViewInit, OnDestroy {
  public _selectedIndex: any;
  public nullImg = 'data:image/gif;base64,R0lGODlhAQABAIABAP///wAAACH5BAEKAAEALAAAAAABAAEAAAICTAEAOw==';
  private _intervalFn = null;
  @ViewChild('slideContainer') slideContainer: ElementRef;
  @ContentChildren(forwardRef(() => LyCarouselItem)) lyItems: QueryList<LyCarouselItem>;
  @Input() mode: CarouselMode = CarouselMode.default;
  @Input() interval = 7000;
  _positionLeft: string | number;
  @Input() selectedIndex = 0;
  selectedElement: HTMLElement;
  classes = this.theme.addStyleSheet(styles, 'lyCarousel');
  private _slideEvent: boolean;
  @Input()
  set slideEvent(val: boolean) {
    const newVal = toBoolean(val);
    this._slideEvent = newVal;
    if (newVal) {
      this.renderer.removeClass(this.elementRef.nativeElement, this.classes.slideNoEvent);
    } else {
      this.renderer.addClass(this.elementRef.nativeElement, this.classes.slideNoEvent);
    }
  }
  get slideEvent() {
    return this._slideEvent;
  }
  onDragStart(e) {
    this.renderer.removeClass(this.slideContainer.nativeElement, this.classes.slideAnim);
    this.selectedElement = this.lyItems.find((item, index) => index === this.selectedIndex)._nativeElement;
  }
  onDrag(e) {
    const rect = this.selectedElement.getBoundingClientRect();
    if (Math.abs(e.deltaX) < rect.width) {
      this._onPan(e.deltaX);
    }
  }
  onDragEnd(e) {
    const rect = this.selectedElement.getBoundingClientRect();
    this.renderer.addClass(this.slideContainer.nativeElement, this.classes.slideAnim);
    this.select(this.selectedIndex);

    if (Math.abs(e.deltaX) > rect.width / 2) {
      if (0 > e.deltaX) {
        this.next();
      } else if (0 < e.deltaX) {
        this.prev();
      }
    } else if (e.additionalEvent) {
      const eventName = e.additionalEvent;
      if (eventName === 'slideleft') {
        this.next();
      } else if (eventName === 'slideright') {
        this.prev();
      }
    }
  }
  constructor(
    private elementRef: ElementRef,
    private sanitizer: DomSanitizer,
    private cd: ChangeDetectorRef,
    private theme: LyTheme2,
    private renderer: Renderer2,
  ) {
    this.renderer.addClass(elementRef.nativeElement, this.classes.root);
  }

  ngOnInit() {
    if (!this.slideEvent) {
      this.slideEvent = false;
    }
    if (Platform.isBrowser) {
      this._resetInterval();
    }
  }

  private _onPan(x) {
    this._positionLeft = this.sanitizerStyle(`translate(calc(${-100 * this.selectedIndex }% + ${x}px), 0px)`) as any;
  }
  private sanitizerStyle(val: any): SafeStyle {
    return this.sanitizer.bypassSecurityTrustStyle(val);
  }

  public ngOnDestroy() {
    if (Platform.isBrowser) {
      this.stop();
    }
  }

  _markForCheck() {
    this.cd.markForCheck();
  }

  ngAfterViewInit() {
    this.renderer.addClass(this.slideContainer.nativeElement, this.classes.slideContainer);
    if (Platform.isBrowser) {
      this.renderer.addClass(this.slideContainer.nativeElement, this.classes.slideAnim);
    }
  }
  select(val: number, notResetInterval?: boolean) {
    this.selectedIndex = val;
    if (this.mode === CarouselMode.default) {
      this._positionLeft = `translate(${-100 * val}%, 0px)`;
    }
    if (!notResetInterval) {
      this._resetInterval();
    }
  }
  prev() {
    const len = this.lyItems.length - 1;
    const prev = this.selectedIndex - 1;
    this.select(prev < 0 ? len : prev);
  }
  next(notResetInterval?: boolean) {
    const len = this.lyItems.length - 1;
    const next = this.selectedIndex + 1;
    this.select(next > len ? 0 : next, notResetInterval);
  }
  private _resetInterval() {
    this.stop();
    this._intervalFn = setInterval(() => {
      this.next(true);
      this.cd.markForCheck();
    }, this.interval);
  }

  stop() {
    if (this._intervalFn !== null) {
      clearInterval(this._intervalFn);
    }
  }
}

@Directive({
  // tslint:disable-next-line:directive-selector
  selector: 'ly-carousel-item'
})
export class LyCarouselItem {
  private _className: string;
  @Input()
  set srcImg(value: string) {
    this._className = this.theme.addStyle(
      `ly-carousel-img:${value}`, (
        `background-image: url('${value}')`
      ),
      this._nativeElement,
      this._className
    );
  }
  _nativeElement: HTMLElement;

  constructor(
    private theme: LyTheme2,
    private renderer: Renderer2,
    elementRef: ElementRef
  ) {
    this._nativeElement = elementRef.nativeElement;
  }

}
